import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import Tab from "./Tab";

const TabDiv = styled.div`
  width: 55%;
  height: 50%;
`;

const TabContent = styled.div`
  width: 100%;
  height: 100%;
`;

const TabList = styled.ul`
  display: flex;
  margin-top: 10px;
  width:100%;
`;

class Tabs extends React.Component {
  static propTypes = {
    children: PropTypes.instanceOf(Array).isRequired
  };

  constructor(props) {
    super(props);
    const TrueTab = this.props.children.filter(child => child && child);
    this.state = {
      activeTab:TrueTab[0].props.label,
      TrueTab :TrueTab
    };
   
  }
  onClickTabItem = tab => {
    this.setState({ activeTab: tab });
  };

  render() {
    const {
      onClickTabItem,
      state: { activeTab,TrueTab }
    } = this;
    return (
     <TabDiv>
        <TabList>
          {TrueTab[0] && TrueTab.map(tab => {
            const { label } = tab.props;

            return (
              <Tab
                activeTab={activeTab}
                key={label}
                label={label}
                onClick={onClickTabItem}
              />
            );
          })}
            </TabList>
          <TabContent>
            {TrueTab[0] && TrueTab.map(tab => {
              if (tab.props.label !== activeTab) return undefined;
              return tab.props.children;
            })}
          </TabContent>
      </TabDiv>
    );
  }
}

export default Tabs;
