import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import Loader from "Components/Loader";
import Helmet from "react-helmet";
import Message from "Components/Message";
import Tabs from "./Tabs/Tabs";
import Poster from "../../Components/Poster";
import { Link } from "react-router-dom";
import ReactPlayer from "react-player";

const Container = styled.div`
  height: calc(100vh - 50px);
  width: 100%;
  position: relative;
  padding: 50px;
`;

const Backdrop = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(${props => props.bgImage});
  background-position: center center;
  background-size: cover;
  filter: blur(3px);
  opacity: 0.5;
  z-index: 0;
`;

const Content = styled.div`
  display: flex;
  width: 100%;
  position: relative;
  z-index: 1;
  height: 100%;
`;

const Cover = styled.div`
  width: 30%;
  background-image: url(${props => props.bgImage});
  background-position: center center;
  background-size: cover;
  height: 100%;
  border-radius: 5px;
`;

const Data = styled.div`
  width: 70%;
  margin-left: 10px;
`;

const Title = styled.h3`
  font-size: 32px;
  margin-bottom: 10px;
`;

const ItemContainer = styled.div`
  margin: 20px 0;
`;

const Item = styled.span`
  font-size: 13pt;
`;

const Divider = styled.span`
  margin: 0 10px;
`;

const Overview = styled.p`
  font-size: 12pt;
  opacity: 0.7;
  line-height: 1.5;
  width: 50%;
`;

const ImdbLink = styled.a`
  all: unset;
  width: 20px;
  height: 10px;
  background-color: rgb(245, 197, 24);
  color: rgb(0, 0, 0);
  font-weight: 900;
  text-transform: capitalize;
  box-sizing: border-box;
  cursor: pointer;
  border-radius: 5px;
  padding: 2px 5px;
`;

const TabResult = styled.div`
  width: 100%;
  box-sizing: border-box;
  padding: 30px 0px;
  transition: all 0.3s ease-in-out 0s;
  ${props => (props.youtube ? "height : 500px; overflow :auto" : null)};
  &::-webkit-scrollbar {
    display: none !important;
  }
`;

const YoutubePlayer = styled(ReactPlayer)`
  :not(:last-child) {
    margin-bottom: 20px;
  }
`;
const TabGridDiv = styled.div`
  margin-top: 25px;
  display: grid;
  grid-template-columns: ${props =>
    props.poster ? "repeat(auto-fill,125px)" : "repeat(auto-fill, 30%)"};
  grid-gap: ${props => (props.poster ? "70px" : "3%")};
  justify-content: center;
`;

const GridDiv = styled.div`
  display: flex;
  flex-direction: column;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  box-sizing: border-box;
  margin: 5px;
  padding: 0px 10px;
`;

const NameSpan = styled.span`
  display: inline-block;
  background-color: rgba(0, 0, 0, 0.5);
  text-align: center;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.8);
  border-radius: 5px;
  padding: 10px;
  margin: 3px;
  ${props => (props.country ? `width : 100%` : null)}
`;
const Img = styled.img`
  width: ${props =>
    props.src !== "/static/media/noPosterSmall.87a0eb6a.png" && props.company
      ? "100%"
      : "80px"};
  height: 100px;
  background-size: cover;
  border-radius: 4px;
  background-position: center center;
  transition: opacity 0.1s linear;
  margin-bottom: 10px;
`;

const CollectionLink = styled(Link)`
  all: unset;
  position: relative;
  left: 20px;
  font-size: 20px;
  background-color: rgb(255, 107, 107);
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  transition: transform 0.3s ease-in-out 0s;
  padding: 10px;
  border-radius: 5px;
`;

const DetailPresenter = ({ result, loading, error }) =>
  loading ? (
    <>
      <Helmet>
        <title>Loading | ChoiFlix</title>
      </Helmet>
      <Loader />
    </>
  ) : error ? (
    <Message text="Can't find result" />
  ) : (
    <Container>
      <Helmet>
        <title>
          {result.original_title ? result.original_title : result.original_name}
        </title>
      </Helmet>
      <Backdrop
        bgImage={`https://image.tmdb.org/t/p/original${result.backdrop_path}`}
      />
      <Content>
        <Cover
          bgImage={
            result.poster_path
              ? `https://image.tmdb.org/t/p/original${result.poster_path}`
              : require("../../asset/noPosterSmall.png")
          }
        />
        <Data>
          <Title>
            {result.original_title
              ? result.original_title
              : result.original_name}
            {result.belongs_to_collection && (
              <CollectionLink
                to={`/collection/${result.belongs_to_collection.id}`}
              >
                Collection
              </CollectionLink>
            )}
          </Title>
          <ItemContainer>
            <Item>
              {result.release_date
                ? result.release_date.substring(0, 4)
                : result.first_air_date.substring(0, 4)}
            </Item>
            <Divider>üîò</Divider>
            <Item>
              {result.runtime ? result.runtime : result.episode_run_time[0]} min
            </Item>
            <Divider>üîò</Divider>
            <Item>
              {result.genres &&
                result.genres.map((genre, index) =>
                  index === result.genres.length - 1
                    ? genre.name
                    : `${genre.name} / `
                )}
            </Item>
            {result.runtime ? (
              <>
                <Divider>üîò</Divider>
                <Item>
                  <ImdbLink
                    href={`https://www.imdb.com/title/${result.imdb_id}`}
                    target="_blank"
                  >
                    IMDB
                  </ImdbLink>
                </Item>
              </>
            ) : null}
          </ItemContainer>
          <Overview>{result.overview && result.overview}</Overview>
          <Tabs>
            {result.videos.results && result.videos.results.length > 0 && (
              <div label="Youtube" key="youtube">
                <TabResult youtube>
                  {result.videos.results.map(video => (
                    <YoutubePlayer
                      url={`https://www.youtube.com/watch?v=${video.key}`}
                      controls
                      width="100%"
                      
                      key={video.key}
                    />
                  ))}
                </TabResult>
              </div>
            )}
            {result.production_companies &&
              result.production_companies.length > 0 && (
                <div label="Production Company" key="company">
                  <TabResult>
                    <TabGridDiv>
                      {result.production_companies.map(company => (
                        <GridDiv key={company.id}>
                          <Img
                            src={
                              company.logo_path !== null
                                ? `https://image.tmdb.org/t/p/original${company.logo_path}`
                                : require("../../asset/noPosterSmall.png")
                            }
                            company
                          />
                          <NameSpan>{company.name}</NameSpan>
                        </GridDiv>
                      ))}
                    </TabGridDiv>
                  </TabResult>
                </div>
              )}
            {result.production_countries &&
              result.production_countries.length > 0 && (
                <div label="Production Countries" key="country">
                  <TabResult>
                    {result.production_countries.map(country => (
                      <NameSpan key={country.name} country>
                        {country.name}
                      </NameSpan>
                    ))}
                  </TabResult>
                </div>
              )}
            {result.created_by && result.created_by.length > 0 && (
              <div label="Created By" key="created">
                <TabResult>
                  <TabGridDiv>
                    {result.created_by.map(create => (
                      <GridDiv key={create.id}>
                        <Img
                          src={
                            create.profile_path !== null
                              ? `https://image.tmdb.org/t/p/original${create.profile_path}`
                              : require("../../asset/noPosterSmall.png")
                          }
                        />
                        <NameSpan>{create.name}</NameSpan>
                      </GridDiv>
                    ))}
                  </TabGridDiv>
                </TabResult>
              </div>
            )}
            {result.seasons && result.seasons.length > 0 && (
              <div label="Seasons">
                <TabResult>
                  <TabGridDiv poster>
                    {result.seasons.map(season => (
                      <Poster
                        id={season.id}
                        isSeason={true}
                        key={season.id}
                        title={season.name}
                        rating={season.episode_count}
                        imageUrl={season.poster_path}
                        year={season.air_date}
                      />
                    ))}
                  </TabGridDiv>
                </TabResult>
              </div>
            )}
          </Tabs>
        </Data>
      </Content>
    </Container>
  );

DetailPresenter.propTypes = {
  result: PropTypes.object.isRequired,
  loading: PropTypes.bool.isRequired,
  error: PropTypes.string,
  tab: PropTypes.string
};

export default DetailPresenter;
