
import React from "react";
import CollectionPresenter from "./CollectionPresenter";
import {collectionsApi} from "../../api";



export default class extends React.Component{

  state  ={
    collection :null,
    error: null,
    loading: true
  }
  
  async componentDidMount() {
    const {
      match: {
        params: { id }
      },
      history: { push }
    } = this.props;
    try{
      const {data :collection }  = await collectionsApi.getCollection(id);
      this.setState({
        collection
      })
    
    }catch{
      this.setState({
        error: "Can't find collection information."
      });
    }finally{
      this.setState({
        loading: false
      });
    }
   
  }

  render(){
    const {collection, error, loading} = this.state;
    return <CollectionPresenter collection ={collection} error={error} loading={loading} />
  }
}
