import React from "react";
import styled from "styled-components";
import PropTypes, { object } from "prop-types";
import Loader from "Components/Loader";
import Message from "Components/Message";
import Poster from "../../Components/Poster";
import Section from "../../Components/Section";

const Backdrop = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(${props => props.bgImage});
  background-position: center center;
  background-size: cover;
  filter: blur(3px);
  opacity: 0.5;
  z-index: 0;
`;

const Container = styled.div`
  height: calc(100vh - 50px);
  width: 100%;
  position: relative;
  padding: 50px;
`;

const Content = styled.div`
  display: flex;
  width: 100%;
  position: relative;
  z-index: 1;
  height: 100%;
`;

const Cover = styled.div`
  width: 30%;
  background-image: url(${props => props.bgImage});
  background-position: center center;
  background-size: cover;
  height: 100%;
  border-radius: 5px;
  
`;

const Data = styled.div`
  width: 70%;
  margin-left: 10px;
`;

const CollectionPresenter = ({ collection, error, loading }) =>
  loading ? (
    <Loader />
  ) : (
    <>
    <Backdrop
    bgImage={`https://image.tmdb.org/t/p/original/${collection.backdrop_path}`}
    />
    <Container>
      <Content>

      <Cover bgImage={collection.poster_path
              ? `https://image.tmdb.org/t/p/original${collection.poster_path}`
              : require("../../asset/noPosterSmall.png") } />
              <Data>

      <Section title={collection.name}>
        {collection.parts.map(part => (
          <Poster
          key={part.id}
          id={part.id}
          title={part.original_title}
          imageUrl={part.poster_path}
          rating={part.vote_average}
          isMovie={true}
          year={part.release_date && part.release_date.substring(0, 4)}
          />
          ))}
      </Section>
          </Data>
          </Content>
    </Container>
    </>
  );

CollectionPresenter.propTypes = {
  collection: PropTypes.object.isRequired,
  loading: PropTypes.bool.isRequired,
  error: PropTypes.string
};

export default CollectionPresenter;
